import { Link } from "gatsby"
import { OutboundLink } from "gatsby-plugin-google-gtag"
import * as React from "react"
import IString from "../components/IString"
import Layout from "../components/Layout"
import TSwitch from "../components/TSwitch"

const data = [
  {
    name: {
      en: 'Strelochka',
      ru: 'Стрелочка'
    },
    since: "2016",
    url: "https://www.strelchka.ru",
    description: {
      en: "Price calendar for Russian Railway tickets.",
      ru: "Календарь цен на ЖД билеты."
    },
    tags: ['civil'],
  },
  {
    name: {
      en: 'Xran',
      ru: 'Хранилище'
    },
    since: "2020",
    url: "https://www.xran.ru",
    description: {
      en: "Make your Telegram into an awesome cloud storage.",
      ru: "Сервис, превращающий Телеграм в самое лучшее облачное хранилище."
    },
    tags: ['civil']
  },
  {
    name: {
      en: 'Taas',
      ru: 'Таас'
    },
    since: "2019",
    url: "https://www.t-a-a-s.ru",
    description: {
      en: "Taas offers managed TDLib as cloud service. TDlib - is a complete headless Telegram client controlled by API - you can get channel history, send messages and many other things, go to Taas site to find out more.",
      ru: "Хотите использовать TDLib но ничего не устанавливать? Таас предоставляет TDLib как облачную услугу. TDLib - это целый headless Телеграм клиент управляемый по API - можно получать историю канала, отправлять сообщения и многое другое, смотри подробнее на сайте."
    },
    tags: [ 'devs' ]
  },
]

const tags = [
  {
    label: {
      en: 'No filter',
      ru: 'Без фильтра'
    },
    value: null
  },
  {
    label: {
      en: 'For civils',
      ru: 'Для всех'
    },
    value: 'civil'
  },
  {
    label: {
      en: 'For devs',
      ru: 'Для разработчиков'
    },
    value: 'devs'
  },
]


// markup
const InetPage = () => {
  const [selectedTag, setSelectedTag] = React.useState();

  const filteredData = React.useMemo(() => {
    if (selectedTag) {
      return data.filter((item) => item.tags.includes(selectedTag))
    } else {
      return data
    }
  }, [selectedTag])

  return (
    <main>
      <Layout>
        <div>
          <Link to="/"><IString ru="Назад" en="Back" /></Link>
        </div>
        <div className="page">
          <TSwitch
            options={tags}
            onChange={(v) => setSelectedTag(v)}
          />
          {filteredData.map((site) => <div className="inet-block">
            <p className="pp">
              <h3><IString {...site.name}/></h3>
              <div className="subtitle">
                <IString ru={`осн. ${site.since} г.`} en={`since ${site.since}`} />
                <div><OutboundLink href={site.url}>{site.url}</OutboundLink></div>
              </div>
              
            </p>
            <p className="pp"><IString {...site.description} /></p>
          </div>)}
        </div>
      </Layout> 
    </main>
  )
}

export default InetPage
