import React from 'react'
import IString from './IString';

const TSwitch = (props) => {
  const {
    options,
    onChange
  } = props;
  const [selected, setSelected] = React.useState(options[0].value);

  const handleSelect = (o) => {
    setSelected(o)
    if (onChange)
      onChange(o)
  }

  return (
    <div className="tswitch">
      {options.map(o => <span
        onClick={() => handleSelect(o.value)}
        className={selected == o.value ? 'tswitch-sel' : 'tswitch-unsel'}
      >
        <IString {...o.label} />
      </span>)}
    </div>
  )
}

export default TSwitch